<template>
    <div class="page-container">
        <el-aside class="page-nav-left">
            <div class="aside-bar">
                <el-menu default-active="3">
                    <router-link to="/farming/task">
                        <el-menu-item index="0">
                            <i class="el-icon-date"></i>
                            <span slot="title">任务管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/farming/seed">
                        <el-menu-item index="1">
                            <i class="el-icon-coin"></i>
                            <span slot="title">种子管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/farming/tour">
                        <el-menu-item index="2">
                            <i class="el-icon-truck"></i>
                            <span slot="title">巡园管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/farming/work">
                        <el-menu-item index="3">
                            <i class="el-icon-notebook-1"></i>
                            <span slot="title">工作汇报</span>
                        </el-menu-item>
                    </router-link>
                </el-menu>
            </div>
        </el-aside>
        <el-main class="m-l-220 m-t-20 bg-white">
            <div class="flex flex-align-center flex-pack-justify">
                <div class="flex flex-align-center">
                    <el-select class="m-r-15" size="small" v-model="vm.searchType" placeholder="请选择汇报类型">
                        <el-option
                            v-for="item in typeList"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                    <el-input class="m-r-15" size="small" style="width:200px;" placeholder="请输入工作汇报" suffix-icon="el-icon-search" v-model="vm.searchKey"></el-input>
                    <el-button type="primary" size="small" @click="search">搜索</el-button>
                </div>
                <el-button type="primary" size="small" @click="add">汇报农事记录</el-button>
            </div>
            <el-row class="m-t-30">
                <el-col :span="24">
                    <el-table :height="tableHeight" :data="dataList" size="small" border stripe style="width: 100%">
                        <el-table-column prop="type" label="农事类型"></el-table-column>
                        <el-table-column prop="org" label="组织"></el-table-column>
                        <el-table-column prop="time" label="农事时间" width="280"></el-table-column>
                        <el-table-column prop="number" label="用工量"> </el-table-column>
                        <el-table-column prop="content" label="内容" show-overflow-tooltip></el-table-column>
                        <el-table-column fixed="right" label="操作" width="140">
                            <template slot-scope="scope">
                                <el-button type="primary" size="mini" icon="el-icon-search" @click="edit(scope.row)"></el-button>
                                <el-button type="danger" size="mini" icon="el-icon-delete" @click="remove(scope.row)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-col>
            </el-row>
            <el-row class="m-t-30">
                <el-col>
                    <el-pagination 
                        class="text-right" 
                        background 
                        layout="total, prev, pager, next" 
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page.sync="vm.currentPage"
                        :page-size="vm.pageSize"
                        :total="vm.pageTotal">
                    </el-pagination>
                </el-col>
            </el-row>
            <el-dialog title="汇报农事记录" :visible.sync="dialogVisible" width="800px" :close-on-click-modal="false">
                <el-form ref="form" size="small" :rules="rules" :model="form" label-width="150px">
                    <el-form-item label="农事类型" prop="type">
                        <el-select style="width:260px;" v-model="form.type" placeholder="请选择汇报类型">
                            <el-option
                                v-for="item in typeList"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="开始时间" prop="startTime">
                        <el-date-picker style="width:260px;" type="datetime" placeholder="选择日期" v-model="form.startTime"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="结束时间" prop="endTime">
                        <el-date-picker style="width:260px;" type="datetime" placeholder="选择日期" v-model="form.endTime"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="用工量（人天）" prop="number">
                        <el-input style="width:260px;" v-model="form.number"></el-input>
                    </el-form-item>
                    <el-form-item label="内容" prop="content">
                        <el-input type="textarea" v-model="form.content"></el-input>
                    </el-form-item>
                    <el-form-item label="拍摄图片" prop="pictures">
                        <el-upload
                            multiple
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :limit="10"
                            :file-list="form.pictures">
                            <el-button size="small" type="primary">点击上传</el-button>
                            <div slot="tip">只能上传jpg/png文件，且不超过500kb</div>
                        </el-upload>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button size="small" @click="dialogVisible = false">取消</el-button>
                    <el-button size="small" type="primary" @click="save">保存</el-button>
                </span>
            </el-dialog>
        </el-main>
    </div>
</template>

<script>
export default {
    data() {
        return {
            vm: {
                searchKey: '',
                searchType: '',
                currentPage: 1,
                pageSize: 10,
                pageTotal: 100,
            },
            typeList: [{
                value: '选项1',
                label: 'sas'
            }, {
                value: '选项2',
                label: '双皮奶'
            }, {
                value: '选项3',
                label: '蚵仔煎'
            }, {
                value: '选项4',
                label: '龙须面'
            }, {
                value: '选项5',
                label: '北京烤鸭'
            }],
            dataList: [{
                type: '农事汇报',
                org: '东里旺旺葡萄园',
                time: '2019-09-23 17:29:00至2019-09-25 17:29:00',
                number: '1人天',
                content: '除草',
            }, {
                type: '施药汇报',
                org: '东里旺旺葡萄园',
                time: '2019-09-23 17:29:00至2019-09-25 17:29:00',
                number: '1人天',
                content: '果串喷药到位，不留死角果串喷药到位，不留死角果串喷药到位，不留死角果串喷药到位，不留死角',
            }, {
                type: '农事汇报',
                org: '东里旺旺葡萄园',
                time: '2019-09-23 17:29:00至2019-09-25 17:29:00',
                number: '1人天',
                content: '加长宝1000倍，全园喷药',
            }, {
                type: '农事汇报',
                org: '东里旺旺葡萄园',
                time: '2019-09-23 17:29:00至2019-09-25 17:29:00',
                number: '1人天',
                content: '除草',
            }],
            dialogVisible: false,
            form:{
                type: '',
                startTime: '',
                endTime: '',
                number: '',
                content: '',
                pictures: []
            },
            rules: {
                type: [
                    { required: true, message: '请选择汇报类型', trigger: 'change' },
                ],
                startTime: [
                    { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
                ],
                endTime: [
                    { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
                ],
                number: [
                    { required: true, message: '请填写用工量（人天）', trigger: 'blur' }
                ],
                content: [
                    { required: true, message: '请填写内容', trigger: 'blur' }
                ],
                pictures: [
                    { type: 'array', required: true, message: '请上传图片', trigger: 'change' }
                ]
            }
        }
    },
    computed:{
        tableHeight() {
            return document.documentElement.clientHeight - 275;
        }
    },
    methods:{
        search(){

        },
        add(){
            this.dialogVisible = true
        },
        edit(row) {
            this.resetForm()
            this.dialogVisible = true;
            this.form = row
        },
        save(){
            this.$refs['form'].validate((valid) => {
                if (valid) {
                    console.log(this.form)
                    this.dialogVisible = false
                } else {
                    this.$message.error("请检查您的输入！");
                    return false;
                }
            });
        },
        remove(row){
            this.$confirm('是否删除？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$message({
                    type: 'success',
                    message: '删除成功!'
                });
            }).catch(() => {
                          
            });
        },
        resetForm() {
            this.form = {
                type: '',
                startTime: '',
                endTime: '',
                number: '',
                content: '',
                pictures: []
            }
        },
        handleSizeChange(val) {
            console.log(val);
        },
        handleCurrentChange(val) {
            console.log(val);
        }
    }
}
</script>

<style>

</style>


